import React from 'react';

import styled from 'styled-components';
import mq from '../../components/MediaQuery';

import parallax1 from '../../assets/img/parallax1.jpg';
import avatarHat from '../../assets/img/avatar_hat.jpg';

const IntroduceContainer = styled.div`
    //background-color: #0603;

    /* 첫 번째 섹션만 다른 영역과 구별되는 특성이 있으므로 그 부분을 보완한다. */
    .title {
        height: 100vh;

        .hello {
            background-color: #616161;
        }
    }

    /* 자기소개 */
    .about-me {
        max-width: 1100px;
        margin: auto;
        padding: 65px 40px;

        ${mq.maxWidth("md")`
            padding: 45px 20px;
        `}

        h2 {
            font-size: 42px;
            color: #444444;
            margin-bottom: 40px;
            text-align: center;

            ${mq.maxWidth("md")`
                font-size: 36px;
                margin-bottom: 20px;
            `}
        }

        h3 {
            font-size: 30px;
            font-weight: 400;
            margin-bottom: 40px;
            font-style: italic;
            color: #616161;
            text-align: center;

            ${mq.maxWidth("md")`
                font-size: 20px;
                margin-bottom: 30px;
                font-weight: 300;
            `}
        }

        p {
            line-height: 150%;
            font-size: 18px;
            margin-bottom: 45px;
            font-weight: 300;

            ${mq.maxWidth("md")`
                line-height: 135%;
                font-size: 16px;
                margin-bottom: 25px;
            `}
        }

        .about-box {
            display: flex;
            flex-wrap: wrap;

            .photo-box {
                width: 50%;
                padding: 0 35px;

                ${mq.maxWidth("md")`
                    width: 100%;
                    padding: 0 20px;
                `}

                h4 {
                    font-size: 22px;
                    color: #616161;
                    margin-bottom: 20px;

                    ${mq.maxWidth("md")`
                        text-align: center;
                    `}

                    i {
                        margin-right: 10px;
                    }
                }

                img {
                    width: 100%;
                }
            }

            p {
                width: 50%;
                padding-top: 40px;

                ${mq.maxWidth("md")`
                    width: 100%;
                `}
            }
        }
    }

    /* 스킬 점수 */
    .skill {
        max-width: 1100px;
        margin: auto;
        padding: 40px 40px 50px 40px;

        h2 {
            font-size: 32px;
            margin-bottom: 45px;
            text-align: center;
            color: #616161;

            ${mq.maxWidth("md")`
                font-size: 28px;
                margin-bottom: 35px;
            `}
        }

        .skill-section {
            h3 {
                font-size: 22px;
                margin-bottom: 15px;
                font-weight: 300;
                letter-spacing: 8px;

                ${mq.maxWidth("md")`
                    font-size: 18px;
                    margin-bottom: 10px;
                `}
            }

            .bar-container {
                height: 35px;
                background-color: #eeeeee;
                margin-bottom: 30px;

                ${mq.maxWidth("md")`
                    height: 25px;
                    margin-bottom: 20px;
                `}

                .bar {
                    height: 100%;
                    background-color: #616161;
                    text-align: center;
                    color: #f1f1f1;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
            }
        }
    }

    /* 상황별 점수 */
    .point {
        background-color: #424242;

        .point-container {
            max-width: 1200px;
            margin: auto;
            padding: 60px 10px;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;

            ${mq.maxWidth("md")`
                padding: 20px 10px;
            `}

            .point-item {
                width: 25%;
                text-align: center;
                color: #f1f1f1;
                margin: 20px auto;

                ${mq.maxWidth("md")`
                    width: 50%;
                `}

                .point-value {
                    font-size: 45px;
                    font-weight: 500;
                    margin-bottom: 25px;

                    ${mq.maxWidth("md")`
                        font-size: 38px;
                        font-weight: 400;
                        margin-bottom: 20px;
                    `}
                }

                .point-text {
                    font-size: 24px;
                    font-weight: 300;

                    ${mq.maxWidth("md")`
                        font-size: 16px;
                    `}
                }
            }
        }
    }
`;

const Introduce = () => {
    return (
        <IntroduceContainer>
            <div className="title" style={{backgroundImage: `url(${parallax1})`}}>
                <div className="hello">
                    <h1>안녕하세요. OOO입니다.</h1>
                    <p>This is my website</p>
                </div>
            </div>

            {/* 자기소개 */}
            <div className="about-me">
                <h2>ABOUT ME</h2>
                <h3>I love photography</h3>
                <p>
                    We have created a fictional "personal" website/blog, and our fictional character is a hobby photographer. Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur.<br />
                    Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.
                </p>
                <div className="about-box">
                    <div className="photo-box">
                        <h4>
                            <i className="fa-solid fa-user"></i>Frontend Student
                        </h4>
                        <img src={avatarHat} />
                    </div>
                    <p>
                        Welcome to my website. I am lorem ipsum consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.
                    </p>
                </div>
            </div>

            {/* 스킬 점수 */}
            <div className="skill">
                <h2>I'm really good at:</h2>
                <div>
                    <div className="skill-section">
                        <h3>
                            <i className={`fa-solid fa-camera`}></i>
                            <span>Photography</span>
                        </h3>
                        <div className="bar-container">
                            <div className='bar' style={{
                                width: `90%`
                            }}>90%</div>
                        </div>
                    </div>
                    <div className="skill-section">
                        <h3>
                            <i className={`fa-solid fa-laptop`}></i>
                            <span>Web Design</span>
                        </h3>
                        <div className="bar-container">
                            <div className='bar' style={{
                                width: `85%`
                            }}>85%</div>
                        </div>
                    </div>
                    <div className="skill-section">
                        <h3>
                            <i className={`fa-solid fa-image`}></i>
                            <span>Photoshop</span>
                        </h3>
                        <div className="bar-container">
                            <div className='bar' style={{
                                width: `75%`
                            }}>75%</div>
                        </div>
                    </div>
                </div>
            </div>

            {/* 상황별 점수 */}
            <div className="point">
                <div className="point-container">
                    <div className="point-item">
                        <div className="point-value">14+</div>
                        <div className="point-text">Partners</div>
                    </div>
                    <div className="point-item">
                        <div className="point-value">55+</div>
                        <div className="point-text">Projects Done</div>
                    </div>
                    <div className="point-item">
                        <div className="point-value">89+</div>
                        <div className="point-text">Happy Clients</div>
                    </div>
                    <div className="point-item">
                        <div className="point-value">150+</div>
                        <div className="point-text">Meetings</div>
                    </div>
                </div>
            </div>
        </IntroduceContainer>
    );
};

export default Introduce;